@font-face {
  font-family: 'funny';
  src:  url('../fonts/funny/funny.eot?286doc');
  src:  url('../fonts/funny/funny.eot?286doc#iefix') format('embedded-opentype'),
  url('../fonts/funny/funny.ttf?286doc') format('truetype'),
  url('../fonts/funny/funny.woff?286doc') format('woff'),
  url('../fonts/funny/funny.svg?286doc#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'funny' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

